              >
            </li>
          </ul>
          <button class="replaceBtn" @click="getData">SEARCH</button>
        </div>
        <div class="main">
          <div class="mainTitle">
            <h2>搜尋結果</h2>
            <div class="orderFilter">
              <span>排序 :</span>
              <img src="@/assets/icon/arrangement.svg" alt="" />
              <select name="order" id="">
                <option value="">熱門度</option>
              </select>
            </div>
          </div>
          <ul class="spotsList" v-if="data[0] !== undefined">
            <li class="spot" v-for="item in modifiedData" :key="item.Name">
              <div class="spotImg">
                <router-link to="/spot">
                  <img
                    :src="item.Picture.PictureUrl1"
                    alt=""
                    @click="$store.commit('GET_DATA', item)"
                  />
                </router-link>
              </div>
              <div class="spotInfo">
                <div class="spotInfoTitle">
                  <h2 class="spotName" @click="$store.commit('GET_DATA', item)">
                    <router-link to="/spot">{{
