<script>
import wrongImg from '../assets/wrong.png';
import SlideShow from '../components/slideShow/index.vue';
import Footer from '../components/footer/index.vue';
import SearchFilter from '../components/searchFilter/index.vue';
import KeySearch from '../components/keySearch/index.vue';
import jsSHA from 'jssha';

export default {
  data() {
    return {
      location: 'Taipei',
      class: '',
      data: [],
      pushedData: [],
      isShow: false,
    };
  },

  components: {
    SlideShow,
    Footer,
    SearchFilter,
    KeySearch,
  },
  computed: {
    modifiedData() {
      let array = [...this.data];
      array.forEach((item) => {
        //景點圖片處理
        if (item.Picture.PictureUrl1 === undefined) {
          item.Picture.PictureUrl1 = wrongImg;
        }
        //景點描述處理
        if (item.Description === undefined) {
          item.Description = item.DescriptionDetail;
        }
        if (item.Description.length > 80) {
          item.Description = item.Description.slice(0, 80) + '...';
        }
        //景點地址處理
        if (item.Address === undefined) {
          item.Address = item.City;
        }
        if (item.Address.length > 6) {
          item.Address = item.Address.slice(0, 6);
        }
        //開放時間處理
        if (
          item.OpenTime === '全天' ||
          item.OpenTime === '08:00-17:30' ||
          item.OpenTime === '隨時可參觀' ||
          item.OpenTime === '7:00~22:00' ||
          item.OpenTime === '10:00~1:00' ||
          item.OpenTime === '整天'
        ) {
          item.OpenTime = '全天候開放';
        } else if (
          item.OpenTime === '星期一- 星期日上午9:00-下午5:00' ||
          item.OpenTime === '星期一~星期日08:30-17:30' ||
          item.OpenTime === '星期一~星期日08:30-:17:30薩拉矛賣店暫停營業'
        ) {
          item.OpenTime = '9:00-17:00';
        } else if (
          item.OpenTime ===
          '週一至週五 9:00-17:00 (午休時間： 12:30-13:00)    週六、週日休館'
        ) {
          item.OpenTime = '9:00-17:00，周末休館';
        } else {
          item.OpenTime = '詳見官網或電話洽詢';
        }
        //分類處理
        if (item.Class1 === undefined) {
          item.Class1 = '未分類';
        }
        if (item.Class1.length > 4) {
          item.Class1 = item.Class1.slice(0, 4);
        }
        //郵遞區號處理
        if (item.ZipCode === undefined) {
          item.ZipCode = '1293';
        }
      });
      return array;
    },
    spotUrl() {
      let url = this.$store.state.defaultUrl;
      return url;
    },
    keyWordUrl() {
      let url = this.$store.state.keyWordUrl;
      return url;
    },
    classUrl() {
      let url = this.$store.state.classUrl;
      return url;
    },
  },
  methods: {
    getAuthorizationHeader() {
      //  填入自己 ID、KEY 開始
      let AppID = '87115fa4197645d591dc6280a3be89a0';
      let AppKey = '17xoWssRJ7HDiACPh89qKLE-NXs';
      //  填入自己 ID、KEY 結束
      let GMTString = new Date().toGMTString();
      let ShaObj = new jsSHA('SHA-1', 'TEXT');
      ShaObj.setHMACKey(AppKey, 'TEXT');
      ShaObj.update('x-date: ' + GMTString);
      let HMAC = ShaObj.getHMAC('B64');
      let Authorization =
        'hmac username="' +
        AppID +
        '", algorithm="hmac-sha1", headers="x-date", signature="' +
        HMAC +
        '"';
      return { Authorization: Authorization, 'X-Date': GMTString };
    },
    getData() {
      this.axios
        .get(this.spotUrl, {
          headers: this.getAuthorizationHeader(),
        })
        .then((item) => {
          this.data = item.data;
          console.log(this.data);
        })
        .catch((err) => {
          console.log(err);
        });
      this.$store.commit('CLEAR_DATA');
    },
    getKeyWordData() {
      console.log(this.$store.state.keyWordUrl);
      this.axios
        .get(this.keyWordUrl, {
          headers: this.getAuthorizationHeader(),
        })
        .then((item) => {
          this.data = item.data;
          console.log(this.data);
        })
        .catch((err) => {
          console.log(err);
        });
      this.$store.commit('CLEAR_DATA');
    },
    getClassData() {
      this.$store.commit('CHANGE_URL');
      this.axios
        .get(this.classUrl, {
          headers: this.getAuthorizationHeader(),
        })
        .then((item) => {
          this.data = item.data;
          if (this.data[0] !== undefined) {
            this.isShow = true;
          }
        })
        .catch((err) => {
          console.log(err);
        });
      this.$store.commit('CLEAR_DATA');
    },
  },
  mounted() {
    this.axios
      .get(this.$store.state.defaultUrl, {
        headers: this.getAuthorizationHeader(),
      })
      .then((item) => {
        this.data = item.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

