<script>
import wrongImg from '../../assets/wrong.png';
import jsSHA from 'jssha';
const topSpotsUrl =
  'https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?%24top=20&%24format=JSON';

export default {
  data() {
    return {
      now: 0,
      spots: [],
      newSpots: [],
    };
  },
  provide() {
    return { spotData: this.newSpots };
  },
  methods: {
    change(num) {
      let limit = this.spots.length - 1;
      this.now += num;

      if (this.now < 0) {
        this.now = limit;
      } else if (this.now > limit) {
        this.now = 0;
      }
    },
    pushNew(item) {
      this.newSpots = item;
    },
    getAuthorizationHeader() {
      //  填入自己 ID、KEY 開始
      let AppID = '87115fa4197645d591dc6280a3be89a0';
      let AppKey = '17xoWssRJ7HDiACPh89qKLE-NXs';
      //  填入自己 ID、KEY 結束
      let GMTString = new Date().toGMTString();
      let ShaObj = new jsSHA('SHA-1', 'TEXT');
      ShaObj.setHMACKey(AppKey, 'TEXT');
      ShaObj.update('x-date: ' + GMTString);
      let HMAC = ShaObj.getHMAC('B64');
      let Authorization =
        'hmac username="' +
        AppID +
        '", algorithm="hmac-sha1", headers="x-date", signature="' +
        HMAC +
        '"';
      return { Authorization: Authorization, 'X-Date': GMTString };
    },
  },
  computed: {
    showImg() {
      const start = this.now - 3;
      const array = this.spots.slice(start).concat(this.spots.slice(0, start));
      return array;
    },
  },

  mounted() {
    this.axios
      .get(topSpotsUrl, {
        headers: this.getAuthorizationHeader(),
      })
      .then((res) => {
        this.spots = res.data;
        this.spots.forEach((item) => {
          if (item.Picture.PictureUrl1 === undefined) {
            item.Picture.PictureUrl1 = wrongImg;
          }
        });
      });
  },
};
</script>
<template src="./template.html"></template>
